@import url(https://fonts.googleapis.com/css?family=Quantico:400,700&display=swap);
.page-title.ant-typography {
  margin: 0 0 48px 0;
}

.time-clock {
  position: relative;
}

.time-clock__mask {
  background: transparent;
  position: absolute;
  height: 100%;
  width: 100%;
  z-index: 1;
  left: 0;
  top: 0;
}

.actions-bar {
  justify-content: space-between;
  grid-auto-flow: column;
  display: grid;
}

.actions-bar__left,
.actions-bar__right {
  grid-auto-flow: column;
  grid-column-gap: 16px;
  display: grid;
}

.game {
  border: 1px solid #d9d9d9;
  background: white;
  border-radius: 4px;
  overflow: hidden;
  display: grid;
  padding: 8px;
  width: 616px;
}

.game__container {
  -webkit-transition: left 0.2s ease;
  transition: left 0.2s ease;
  grid-auto-flow: column;
  position: relative;
  display: grid;
}

.arrow-key {
  -webkit-transition: background 0.2s ease;
  transition: background 0.2s ease;
  border-radius: 100%;
  height: 100px;
  width: 100px;
  padding: 8px;
}

.arrow-key--success {
  background: green;
}

.arrow-key--error {
  background: red;
}

.arrow-key--collapse {
  width: 0;
}

.onboarding .ant-modal-footer .ant-btn:first-of-type {
  display: none;
}

.onboarding__picture {
  object-fit: contain;
  height: 350px;
}

.onboarding__checkbox {
  position: relative;
  bottom: -60px;
}

.tag.ant-tag {
  margin-right: 0;
}
.card-wrapper h2.ant-typography {
  margin: 0;
}

.score-modal {
  grid-row-gap: 16px;
  display: grid;
}

.score-cards {
  display: grid;
  grid-auto-flow: column;
  justify-content: space-between;
  grid-column-gap: 16px;
  grid-template-columns: 1fr 1fr;
}

.contact-footer {
  justify-content: space-between;
  grid-auto-flow: column;
  align-items: center;
  margin-top: 48px;
  display: grid;
}

.contact-footer__left,
.contact-footer__right {
  grid-auto-flow: column;
  grid-column-gap: 8px;
  align-items: center;
  display: grid;
}

.contact-footer__left__span {
  color: grey;
}

.contact-footer__right__link-picture {
  -webkit-transition: background-image 0.2s ease;
  transition: background-image 0.2s ease;
  background-size: contain;
  height: 50px;
  width: 50px;
}

.contact-footer__right__link-picture--instagram {
  background-image: url("/instagram.png");
}

.contact-footer__right__link-picture--linkedin {
  background-image: url("/linkedin.png");
}

.contact-footer__right__link-picture--twitter {
  background-image: url("/twitter.png");
}

.contact-footer__right__link-picture--github {
  background-image: url("/github.png");
}

.contact-footer__right__link-picture--instagram:hover {
  background-image: url("/instagram-color.png");
}

.contact-footer__right__link-picture--linkedin:hover {
  background-image: url("/linkedin-color.png");
}

.contact-footer__right__link-picture--twitter:hover {
  background-image: url("/twitter-color.png");
}

.contact-footer__right__link-picture--github:hover {
  background-image: url("/github-color.png");
}

.aplication {
  align-content: flex-start;
  justify-content: center;
  grid-row-gap: 16px;
  display: grid;
  width: 100%;
}

